import { DateTime } from 'luxon';

export class LoginRequest {
    token = ''; // se presente, sto facendo una login con token, non ci sará il check password
    appId = 'M4'; // identificativo dell'applicazione che sta effettuando la login
    accountName = '';
    password = '';
    subscriptionKey!: string; // login su una specifica Subscription, a seconda dell'appid potrebbe non essere presente
    processID = ''; // codice identificativo dello specifico processo di login
    otPassword = ''; // otp
    overwriteLogin = false; // as namedcal
    version = 3; //La versione mi servirá da qui in avanti per modificare il comportamento del backend di login che non riesco a gestire in modalita retrocompatibile. Parto da 3 perche é il numero perfetto.
}

export class ChangePasswordInfo {
    AccountName = '';
    Password = '';
    NewPassword = '';
    IgnoreOldPassword = false;
    JWTToken = '';
}

export interface CUCalendarJob {
    jobid: number;
    calendarid: number;
    subscriptionkey: string;
    customer: string;
    instancekey: string;
    currentmap: string;
    destinationmap: string;
    scheduledtime: string;
    estimatedupgradetime: number;
    realupgradetime: number;
    status: number;
    notes: string;
    creationdate: Date;
}
